import FooterComponent from './common-components/footer'
import List from './common-components/list'

export default {
  name: 'user',
  data () {
    return {
      linkActived: '/user',
      userName:"",
      projectEngage: "",
      restMoney:"",
      projectInitiate:"",
      parent:"User",
      project:[],
      userID:""
    }
  },
  created: function () {
    var that = this;
    this.axios.get("/user").then(function (res) {
      console.log(res);
      var data = res.data;
      that.userName = data.userName;
      that.projectEngage = data.userProject.length;
      that.restMoney = data.fortune;
      that.projectInitiate = data.projectInitiate;
      that.userID = data.userId;
      for(let i =0;i<that.projectEngage; i++){
        that.project.pop();
      }
      for(let i =0;i<that.projectEngage; i++){
        that.project.push(data.userProject[i]);
      }
      console.log(that.project)
    }).catch(function (err) {
      if(err=="Error: Request failed with status code 404"||err=="Error: Request failed with status code 504"){
        console.log("");
        that.$router.push({name: 'Login'});
      }
      console.log("err"+err);
    });

    this.axios.get("/engageList").then(function (res) {
      console.log(res);
    }).catch(function (err) {
      console.log("err"+err);
    });
